import React, {Component} from 'react';
import {Route, Redirect, BrowserRouter as Router, Switch} from "react-router-dom";
import ArticleList from '../Article/ArticleList/ArticleList';
import ArticlePage from '../Article/ArticlePage/ArticlePage';
import Header from '../Header/Header';
import HeaderNav from '../Header/HeaderNav/HeaderNav';
import NotFound from '../NotFound/NotFound';
// import Footer from '../Footer/Footer';
import './App.css';

class App extends Component {
    render() {
        return (
            <div className="App">
                <Router>
                    <Header/>
                    <HeaderNav/>
                    <div>
                        <Switch>
                            <Route exact path="/" render={() => <Redirect to='/post'/>}/>
                            <Route exact path="/post" component={ArticleList}/>
                            <Route path="/post/:type/:hash" component={ArticlePage}/>
                            <Route exact path="/404" component={NotFound}/>
                        </Switch>
                    </div>
                </Router>
            </div>
        );
    }
}

export default App;

