import showdown from 'showdown';
import hljs from 'highlight.js';

showdown.extension('highlight', () => {
    const htmlUnEncode = text => {
        return (
            text
                .replace(/&amp;/g, '&')
                .replace(/&lt;/g, '<')
                .replace(/&gt;/g, '>')
        );
    }
    return [{
        type: "output",
        filter: (text, converter, options) => {
            const left = "<pre><code\\b[^>]*>",
                right = "</code></pre>",
                flags = "g";
            const replacement = (wholeMatch, match, left, right) => {
                const lang = (left.match(/class=\"([^ \"]+)/) || [])[1];
                left = left.slice(0, 18) + 'hljs ' + left.slice(18);
                match = htmlUnEncode(match);
                if (lang && hljs.getLanguage(lang)) {
                    return left + hljs.highlight(lang, match).value + right;
                } else {
                    return left + hljs.highlightAuto(match).value + right;
                }
            };
            return showdown.helper.replaceRecursiveRegExp(text, replacement, left, right, flags);
        }
    }];
});


const converter = (options = {metadata: true, extensions: ['highlight']}) => {
    return new showdown.Converter(options)
};
const date_formatter = (date) => {
    const months = ["January", "February", "March","April", "May", "Jun", "July", "August", "September", "October", "November", "December"];
    let current_datetime = new Date(date)
    let formatted_date = months[current_datetime.getMonth()] + " " +  current_datetime.getDate() + ", " + current_datetime.getFullYear()
    return formatted_date;
}

export { converter, date_formatter }