import React, {Component} from 'react';
import axios from 'axios';
import {Redirect} from 'react-router-dom';
import './ArticleList.css';
import ArticleItem from "./ArticleListItem/ArticleItem";
import Paginator from './Paginator';
import querystring from 'querystring';


export default class ArticleList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {
                articleList: [],
                totalCount: 0
            },
            perPage: 10
        }
    }

    setCurrentPageFromQueryString = (props) => {
        const querys = querystring.parse(props.location.search.split("?")[1])
        const pageNumber = querys.page;
        return pageNumber;
    }

    setStateForPageQuery = (props) => {
        const pageNumber = this.setCurrentPageFromQueryString(props);
        if (!!pageNumber) {
            this.setState({currentPage: parseInt(pageNumber)})
        } else {
            this.setState({currentPage: 1})
        }
        return pageNumber;
    }

    componentWillReceiveProps(nextProps, nextContext) {
        this.setStateForPageQuery(nextProps);
    }

    componentWillMount() {
        const pageNumber = this.setStateForPageQuery(this.props);
        axios.get('/static/post/index.json')
            .then(res => this.setState({data: res.data}))
            .then(() => {
                    if (this.pageCalculation() < pageNumber) {
                    console.log('this is render redirect')
                    this.renderRedirect()
                }
                //TODO : page not found err
            })
    }

    renderRedirect = () => {
        return <Redirect to='/404'/>
    };
    pageCalculation = () => {
        const totalCount = this.state.data.totalCount;
        const perPage = this.state.perPage;
        // return parseInt(totalCount / perPage) + (totalCount % perPage ? 1 : 0);
        return parseInt(totalCount / perPage);
    };

    pagination(articleList) {
        /**
         *  hasPrevious, [ list ], HasNext
         *
         *
         *
         *
         * */

        const articleElements = [];
        const paginators = [];
        const totalNumPageCount = 2;
        const zero = 1;
        const perPage = this.state.perPage;
        const currentPage = this.state.currentPage;
        const maxPage = this.pageCalculation();
        let startIndex;
        let endIndex;
        if (currentPage <= totalNumPageCount) {
            startIndex = zero;
            endIndex   = currentPage + totalNumPageCount;
        } else if (currentPage + totalNumPageCount > maxPage) {
            startIndex = maxPage - 4;
            endIndex = maxPage;
        } else {
            startIndex = currentPage - totalNumPageCount;
            endIndex = currentPage + totalNumPageCount;
        }

        if(currentPage <= totalNumPageCount) endIndex = 5;
        const hasPrevious = startIndex === zero ? false : true;
        const hasNext = currentPage === maxPage ? false : true;
        if (hasPrevious) {
            paginators.push(<Paginator pageNumber={0} content="<<" key={0}/>)
        }
        if (!!articleList.length) {
            for (let index = currentPage * perPage; index < currentPage * perPage + 10; index++) {
                const articleInfo = articleList[index];
                console.log('articleInfo', articleInfo)
                if(articleInfo){
                    articleElements.push(<ArticleItem {...articleInfo} key={index}/>);
                }
            }
        }
        for( let i = startIndex; i <= endIndex; i++ ) {
            paginators.push(<Paginator flag={true} key={i} pageNumber={i} content={i}/>)
        }
        if (hasNext) {
            paginators.push(<Paginator flag={true} key={maxPage} pageNumber={maxPage} content=">>" key={111}/>)
        }
        return {
            articleElements,
            hasNext: <Paginator flag={hasNext} pageNumber={endIndex} content="Next"/>,
            hasPrevious: <Paginator flag={hasPrevious} pageNumber={1} content="Previous"/>,
            test: paginators
        }
    }

    render() {
        const articleData = this.pagination(this.state.data.articleList);
        return (
            <div>
                <div className="article-list">
                    {articleData.articleElements}
                </div>
                <div className="pagination">
                    {articleData.test}
                    {/*{articleData.hasPrevious}*/}
                    {/*{articleData.hasNext}*/}
                </div>
            </div>
        )
    }
}