import React, {Component} from 'react';
import axios from 'axios';
import { converter, date_formatter } from '../../Util/converter';
import './ArticlePage.css';
import 'highlight.js/styles/github.css';


export default class ArticlePage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            meta_data: "",
            html_data: ""
        }
    }

    componentDidMount() {
        axios.get(`/static/post/${this.props.match.params.type}/${this.props.match.params.hash}.md`)
            .then(res => {
                const md_data = res.data;
                const my_converter = converter();
                const html_data = my_converter.makeHtml(md_data);
                const meta_data = my_converter.getMetadata();
                this.setState({ meta_data, html_data })
            })
    }

    render() {
        return (
            <div>
                <div className="article-root">
                    <div className="article-header">
                        <h1 className="item-center content-header">{this.state.meta_data.title}</h1>
                        <p className="item-center content-header-date">{date_formatter(this.state.meta_data.date)}</p>
                    </div>
                    <div className="article-content"  dangerouslySetInnerHTML={{__html: this.state.html_data}}>

                    </div>
                </div>
            </div>
        );
    }
}